'use client'

import { useState, useCallback } from 'react'
import { useDropzone } from 'react-dropzone'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import Link from 'next/link'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { z } from 'zod'

const importSchema = z.object({
  phoneColumn: z.string().min(1, 'Phone column is required'),
  nameColumn: z.string().optional(),
})

type ImportForm = z.infer<typeof importSchema>

interface ParsedData {
  headers: string[]
  rows: any[][]
  totalRows: number
}

interface ValidationResult {
  validContacts: number
  invalidContacts: number
  duplicateContacts: number
  errors: string[]
}

export default function ImportPage() {
  const [uploadedFile, setUploadedFile] = useState<File | null>(null)
  const [parsedData, setParsedData] = useState<ParsedData | null>(null)
  const [validationResult, setValidationResult] = useState<ValidationResult | null>(null)
  const [isProcessing, setIsProcessing] = useState(false)
  const [importProgress, setImportProgress] = useState(0)
  const [importComplete, setImportComplete] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
  } = useForm<ImportForm>({
    resolver: zodResolver(importSchema),
  })

  const phoneColumn = watch('phoneColumn')
  const nameColumn = watch('nameColumn')

  const onDrop = useCallback(async (acceptedFiles: File[]) => {
    const file = acceptedFiles[0]
    if (!file) return

    setUploadedFile(file)
    setIsProcessing(true)

    try {
      const formData = new FormData()
      formData.append('file', file)

      const response = await fetch('/api/import/parse', {
        method: 'POST',
        body: formData,
      })

      const result = await response.json()

      if (response.ok && result.success) {
        setParsedData(result.data)
      } else {
        console.error('Parse error:', result.error)
      }
    } catch (error) {
      console.error('Upload error:', error)
    } finally {
      setIsProcessing(false)
    }
  }, [])

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'text/csv': ['.csv'],
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': ['.xlsx'],
      'application/vnd.ms-excel': ['.xls'],
    },
    maxFiles: 1,
  })

  const validateData = async () => {
    if (!parsedData || !phoneColumn) return

    setIsProcessing(true)

    try {
      const response = await fetch('/api/import/validate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          headers: parsedData.headers,
          rows: parsedData.rows,
          phoneColumn,
          nameColumn,
        }),
      })

      const result = await response.json()

      if (response.ok && result.success) {
        setValidationResult(result.data)
      } else {
        console.error('Validation error:', result.error)
      }
    } catch (error) {
      console.error('Validation error:', error)
    } finally {
      setIsProcessing(false)
    }
  }

  const onSubmit = async (data: ImportForm) => {
    if (!parsedData || !validationResult) return

    setIsProcessing(true)
    setImportProgress(0)

    try {
      const response = await fetch('/api/import/contacts', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          headers: parsedData.headers,
          rows: parsedData.rows,
          phoneColumn: data.phoneColumn,
          nameColumn: data.nameColumn,
        }),
      })

      const result = await response.json()

      if (response.ok && result.success) {
        setImportProgress(100)
        setImportComplete(true)
      } else {
        console.error('Import error:', result.error)
      }
    } catch (error) {
      console.error('Import error:', error)
    } finally {
      setIsProcessing(false)
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <Link href="/" className="text-2xl font-bold text-gray-900">
                WhatsApp Campaign Manager
              </Link>
            </div>
            <Link href="/">
              <Button variant="ghost">Back to Dashboard</Button>
            </Link>
          </div>
        </div>
      </header>

      <main className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Import Contacts</h1>
          <p className="text-lg text-gray-600">
            Upload CSV or Excel files to import contacts for your WhatsApp campaigns
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Main Import Flow */}
          <div className="lg:col-span-2 space-y-6">
            {/* Step 1: File Upload */}
            <Card>
              <CardHeader>
                <CardTitle>Step 1: Upload File</CardTitle>
                <CardDescription>
                  Upload a CSV or Excel file containing your contact data
                </CardDescription>
              </CardHeader>
              <CardContent>
                {!uploadedFile ? (
                  <div
                    {...getRootProps()}
                    className={`border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors ${
                      isDragActive
                        ? 'border-blue-400 bg-blue-50'
                        : 'border-gray-300 hover:border-gray-400'
                    }`}
                  >
                    <input {...getInputProps()} />
                    <div className="space-y-4">
                      <div className="text-4xl">üìÅ</div>
                      <div>
                        <p className="text-lg font-medium">
                          {isDragActive
                            ? 'Drop the file here'
                            : 'Drag & drop a file here, or click to select'}
                        </p>
                        <p className="text-sm text-gray-600 mt-2">
                          Supports CSV, XLS, and XLSX files up to 10MB
                        </p>
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <div className="flex items-center justify-between p-4 bg-green-50 border border-green-200 rounded-lg">
                      <div className="flex items-center space-x-3">
                        <div className="text-2xl">üìÑ</div>
                        <div>
                          <p className="font-medium">{uploadedFile.name}</p>
                          <p className="text-sm text-gray-600">
                            {(uploadedFile.size / 1024 / 1024).toFixed(2)} MB
                          </p>
                        </div>
                      </div>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => {
                          setUploadedFile(null)
                          setParsedData(null)
                          setValidationResult(null)
                          setImportComplete(false)
                        }}
                      >
                        Remove
                      </Button>
                    </div>

                    {isProcessing && (
                      <div className="space-y-2">
                        <div className="flex justify-between text-sm">
                          <span>Processing file...</span>
                        </div>
                        <Progress value={50} />
                      </div>
                    )}
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Step 2: Column Mapping */}
            {parsedData && (
              <Card>
                <CardHeader>
                  <CardTitle>Step 2: Map Columns</CardTitle>
                  <CardDescription>
                    Map your file columns to contact fields
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="phoneColumn">Phone Number Column *</Label>
                      <Select onValueChange={(value) => setValue('phoneColumn', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select phone column" />
                        </SelectTrigger>
                        <SelectContent>
                          {parsedData.headers.map((header, index) => (
                            <SelectItem key={index} value={header}>
                              {header}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      {errors.phoneColumn && (
                        <p className="text-sm text-red-600 mt-1">{errors.phoneColumn.message}</p>
                      )}
                    </div>

                    <div>
                      <Label htmlFor="nameColumn">Name Column (Optional)</Label>
                      <Select onValueChange={(value) => setValue('nameColumn', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select name column" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="">None</SelectItem>
                          {parsedData.headers.map((header, index) => (
                            <SelectItem key={index} value={header}>
                              {header}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  </div>

                  <Alert>
                    <AlertDescription>
                      Phone numbers will be automatically validated and converted to E.164 format. 
                      Duplicate numbers will be detected and removed.
                    </AlertDescription>
                  </Alert>

                  <Button onClick={validateData} disabled={!phoneColumn || isProcessing}>
                    {isProcessing ? 'Validating...' : 'Validate Data'}
                  </Button>
                </CardContent>
              </Card>
            )}

            {/* Step 3: Validation Results */}
            {validationResult && (
              <Card>
                <CardHeader>
                  <CardTitle>Step 3: Validation Results</CardTitle>
                  <CardDescription>
                    Review the validation results before importing
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-3 gap-4">
                    <div className="text-center p-4 bg-green-50 rounded-lg">
                      <div className="text-2xl font-bold text-green-600">
                        {validationResult.validContacts}
                      </div>
                      <div className="text-sm text-green-700">Valid Contacts</div>
                    </div>
                    <div className="text-center p-4 bg-red-50 rounded-lg">
                      <div className="text-2xl font-bold text-red-600">
                        {validationResult.invalidContacts}
                      </div>
                      <div className="text-sm text-red-700">Invalid Numbers</div>
                    </div>
                    <div className="text-center p-4 bg-yellow-50 rounded-lg">
                      <div className="text-2xl font-bold text-yellow-600">
                        {validationResult.duplicateContacts}
                      </div>
                      <div className="text-sm text-yellow-700">Duplicates</div>
                    </div>
                  </div>

                  {validationResult.errors.length > 0 && (
                    <Alert variant="destructive">
                      <AlertDescription>
                        <div className="space-y-1">
                          <p className="font-medium">Validation Errors:</p>
                          {validationResult.errors.slice(0, 5).map((error, index) => (
                            <p key={index} className="text-sm">‚Ä¢ {error}</p>
                          ))}
                          {validationResult.errors.length > 5 && (
                            <p className="text-sm">... and {validationResult.errors.length - 5} more</p>
                          )}
                        </div>
                      </AlertDescription>
                    </Alert>
                  )}

                  {validationResult.validContacts > 0 && (
                    <Button onClick={handleSubmit(onSubmit)} disabled={isProcessing} className="w-full">
                      {isProcessing ? 'Importing...' : `Import ${validationResult.validContacts} Contacts`}
                    </Button>
                  )}
                </CardContent>
              </Card>
            )}

            {/* Import Progress */}
            {isProcessing && importProgress > 0 && (
              <Card>
                <CardHeader>
                  <CardTitle>Import Progress</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>Importing contacts...</span>
                      <span>{importProgress}%</span>
                    </div>
                    <Progress value={importProgress} />
                  </div>
                </CardContent>
              </Card>
            )}

            {/* Import Complete */}
            {importComplete && (
              <Card>
                <CardHeader>
                  <CardTitle>Import Complete</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <Alert>
                    <AlertDescription>
                      Your contacts have been successfully imported and are ready for campaigns.
                    </AlertDescription>
                  </Alert>

                  <div className="flex space-x-2">
                    <Link href="/campaigns">
                      <Button>Create Campaign</Button>
                    </Link>
                    <Button
                      variant="outline"
                      onClick={() => {
                        setUploadedFile(null)
                        setParsedData(null)
                        setValidationResult(null)
                        setImportComplete(false)
                        setImportProgress(0)
                      }}
                    >
                      Import More Contacts
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Instructions Sidebar */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">File Requirements</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4 text-sm">
                <div>
                  <h4 className="font-medium mb-2">Supported Formats</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ CSV (.csv)</li>
                    <li>‚Ä¢ Excel (.xlsx, .xls)</li>
                    <li>‚Ä¢ Maximum file size: 10MB</li>
                  </ul>
                </div>

                <Separator />

                <div>
                  <h4 className="font-medium mb-2">Phone Number Format</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Include country code</li>
                    <li>‚Ä¢ Examples: +1234567890</li>
                    <li>‚Ä¢ Or: 1234567890</li>
                    <li>‚Ä¢ System auto-formats to E.164</li>
                  </ul>
                </div>

                <Separator />

                <div>
                  <h4 className="font-medium mb-2">Data Processing</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Automatic validation</li>
                    <li>‚Ä¢ Duplicate detection</li>
                    <li>‚Ä¢ Invalid number filtering</li>
                    <li>‚Ä¢ Opt-in status tracking</li>
                  </ul>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Sample Data Preview</CardTitle>
              </CardHeader>
              <CardContent>
                {parsedData && (
                  <div className="space-y-4">
                    <div>
                      <p className="text-sm font-medium mb-2">
                        Found {parsedData.totalRows} rows with {parsedData.headers.length} columns
                      </p>
                      <div className="text-xs space-y-1">
                        <p><strong>Columns:</strong></p>
                        {parsedData.headers.map((header, index) => (
                          <Badge key={index} variant="secondary" className="mr-1 mb-1">
                            {header}
                          </Badge>
                        ))}
                      </div>
                    </div>

                    <div className="max-h-48 overflow-auto">
                      <Table>
                        <TableHeader>
                          <TableRow>
                            {parsedData.headers.slice(0, 3).map((header, index) => (
                              <TableHead key={index} className="text-xs">
                                {header}
                              </TableHead>
                            ))}
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {parsedData.rows.slice(0, 5).map((row, rowIndex) => (
                            <TableRow key={rowIndex}>
                              {row.slice(0, 3).map((cell, cellIndex) => (
                                <TableCell key={cellIndex} className="text-xs">
                                  {String(cell).substring(0, 20)}
                                  {String(cell).length > 20 && '...'}
                                </TableCell>
                              ))}
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  )
}
